.feature {
    color: $feature-color;
    background-color: $feature-bg;
    .feature-title {
        font-size: $font-h3;
        text-align: center;
        padding: 40px 20px;
        line-height: 1.5;
    }
    li {
        float: left;
        width: 98%;
        margin: 1%;  
        text-align: center;
    }
    img {
        border-radius: 50%;
    }
    h5 {
        padding-top: 20px;
        font-size: $font-h5;
    }
    p {
        padding: 20px;
        text-align: initial;
    }
    @include pad {
        li {
            width: 31.3333%;        
        }
    }
}
.chef {
    background: $chef-bg;
    color: $chef-color;
    .chef-img img{
        width: 100%;
    }
    .today-chef {
        width: 100%;
        padding: 25px;
    }
    h4 {
        font-size: $font-h4;
        line-height: 32px;
    }
    .chef-introduce {
        padding-top: 15px;
        line-height: 1.2;
    }
    @include pad {
        .chef-img,.today-chef {
            float: left;
            width: 50%;
        }  
        .today-chef {
            padding: 25px 25px 0 25px;
        }
    }
    @include large-desktop {
        h4 {
            padding-top: 80px;
        }
        .chef-introduce {
            line-height: 1.5;
        }
    }
}
.reviews {
    padding: 50px 0;
    color: $reviews-color;
    background: $reviews-bg;
    h3 {
        padding: 0px 15px 30px 15px;
        font-size: $font-h3;
        text-align: center;
    }
    li {
        padding: 15px;
    }
    .avater-img ,.avater-info {
        float: left;
    }
    .avater-img {
        width: 15%;
    }
    img {
        border-radius: 50%;
        margin-right: 13px;
    }
    .avater-info {
        width: 85%;
        padding: 0px 13px;
    }
    h5 {
        font-size: $font-h5;
        padding: 10px 0px;
    }
    .title{
        text-align: right;
        margin-top: 15px;
    }
    @include pad {
        li {
            float: left;
            width: 33.33%;
        }
    }
}
.reservations {
    padding: 50px 10px;
    color: $reservation-color;
    background: $reservation-bg;
    h3 {
        font-size: $font-h3;
        text-align: center;
        margin-bottom: 38px;
    }
    .map img {
        width: 100%;
    }
    .input-contact {
        input[type="text"],
        input[type="tel"],
        input[type="email"],
        input[type="number"] {
            width: 100%;
            margin-bottom: 16px;
        }
        input[type="radio"] {
            display: inline-block;
        }   
    }
    .submit-btn {
        text-align: right; 
        .btn-cancel{
            margin-right: 10px;
        }
        .btn-send {
            margin: 0px;
        }
    }
    .vegan {
        display: inline-block;
        margin-right: 36px;
    }
    
    @include pad {
        .map img ,
        .input-contact {
            float: left;
            width: 50%;
        }
        .map img {
            height: 380px;
        }
        .input-contact {
            padding: 0 15px;
        }

    }
}