.content-login {
    background: $login-bg;
}
.login {
    max-width: 400px;
    margin: 0 auto;
    padding: 50px 15px; 
    color: $login-color;
    .login-logo,h4,h5 {
        text-align: center;
    }
    h4 {
        font-size: $font-h4;
        margin: 5px 15px 30px 15px;
    }
    h5 {
		font-size: $font-h5;
		font-weight: bold;
    }
    .login-form {
        padding: 15px;
        border-top : 3px solid $login-border;
        background: #EFE9E7;
        box-shadow: 3px 3px 4px 0px rgba(0,0,0,0.16);
		.email-block,
		.pw-block {
			position: relative;
		}
		.icon-mail,
		.icon-pw {
			position: absolute;
			top: 32px;
			left: 10px;
		}
		.email,
        .password {
			width: 100%;
			padding-left: 33px;
        }
        .email {
            margin-bottom: 15px;
        }
        .forget {
            color: $login-forget;
        }
    }
    .login-btn {
        margin-top: 36px;
        button {
            width: 47%;
        }
        .btn-register {
            float: left;
        }
        .btn-send {
            float: right;
            margin: 0;
        }
    }
    
}