.content-cart {
    background: $cart-bg;
}
.order-food {
    padding: 50px 15px 20px 15px;
    color: $food-color;
    h4 {
        font-size: $font-h4;
        i {
            margin: 0 10px;
        }
    }
    li {
        border-bottom: 1px solid $food-color;
    }
    a {
        display: block;
        padding: 10px;
        text-decoration: none;
        color: $food-color;
        &:hover,
        &:active {
            background: $food-color;
            color: $food-hover;
        }
    }
    .order-value {
        margin-left: 5px;
    }
    @include pad {
        width: 26%;
        float: left;
    }
}

.foods {
    padding: 15px;
    @include pad {
        width: 74%;
        float: right;
        padding: 48px 15px 15px 15px;
    }
}
.foods .search-block {
    input,
    button {
        float: left;
    }
    .food-search {
        display: inline-block;
        width: 90%;
        height: 34px;
        border: 2px solid $search-input;
        border-right: transparent;
        border-radius: 0;
        &:focus {
            border-color: $search-input-focus;
        }
    }
    button {
        width: 10%;
        height: 34px;
        background: $food-color;
        border-color: transparent;
        cursor: pointer;
        &:active,
        &:hover {
            background: $search-btn-hover;
        }
    }
    .btn-search {
        padding: 6px 2px;
        color: $search-btn;
    }
    @include desktop {
        width: 75%;
        float: left;
    }
}
.checkout {
    float: right;
    margin: 20px 0;
    cursor: pointer;
    .go-checkout {
        border-right: 1px solid black;
        padding-right: 10px;
    }
    .cart {
        position: relative;
    }
    .cart-icon {
        padding-left: 5px;
        font-size: 22px;
    }
    .cart-num {
        position: absolute;
        top: -10px;
        right: -10px;
        @include circle(15px);
        background: $cartNum-bg;
        text-align: center;
        color: $cartNum-color;
        font-size: $font-cartNum;
    }
    &:hover,
    &:active {
        .go-checkout,
        .cart-icon {
            color: $cart-color-hover;
        }
        .cart-num {   
            background: $cartNum-bg-hover;
        }
    }
    @include desktop {
        width: 16%;
        margin: 0;
    }
}

.food-list {
    li {
        margin-bottom: 30px;
        box-shadow: 3px 3px 4px 0px rgba(0,0,0,0.16);
        overflow: hidden;
    }
    .classic-burger {
        background-image: url(../images/product_1.png);
        @include list-bg(120px);
    }
    .handsome-sausage {
        background-image: url(../images/product_2.png);
        @include list-bg(120px);
    }
    .hamburg-brothers {
        background-image: url(../images/product_3.png);
        @include list-bg(120px);
    }
    .dragon-ball {
        background-image: url(../images/product_4.png);
        @include list-bg(120px);
    }
    .item-img {
        position: relative;
        color: $item-img-color;
        transition: 0.5s linear;
        .icon-heart {
            position: absolute;
            top: 10px;
            left: 10px;        
        }
        .fa-heart {
            font-size: $font-heart;
        }
        .sell-well-bg {
            position: absolute;
            top: 0px;
            right: 0px;
            border-style: solid;
            border-width: 0 70px 70px 0;
            border-color: transparent $item-img-bg transparent transparent; 
        }
        .sell-well-text {
            position: absolute;
            top: 18px;
            right: -5px;
            transform: rotate(45deg);
            font-size: 14px;
        }
        &:hover {
            transform: scale(1.1);
        }
    }
    .item-content {
        padding: 10px 15px;
        color: $item-clolr;
        text-align: center;
        h4 {
            font-size: $font-h4;
        }
    }
    .item-en-name {
        font-size: $font-en-name;
    }
    .item-line {
        margin: 10px auto;
        width: 100px;
        border: 1px solid;
    }
    .amount {
        font-size: $font-amount;
        margin: 10px;
    }
    .btn-cart {
        width: 100%;
        .cart-icon {
            margin-right: 10px;
        }
    }
    @include pad {
        li {
            float: left;
            width: 48%;
            margin: 0 1% 3% 1%;
        }
    }
    @include desktop {
        margin-top: 30px;
        li {
            width: 31.3333%;
        }
    }
}