@mixin hide-text {
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap;
}

@mixin circle($size) {
    width: $size;
    height: $size;
    border-radius: 50%;
}

@mixin list-bg($height){
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: $height;
}

@mixin iphone5-landscape {
    @media(min-width: 568px){
        @content
    }
}

@mixin pad {
    @media(min-width: 767px){
        @content
    }
}
@mixin desktop {
    @media(min-width: 1000px){
        @content
    }
}
@mixin large-desktop {
    @media(min-width: 1200px){
        @content
    }
}

